.todoListMain {
  border:1px solid #f6f6f0;
  background-color: #467de8;
  .header {
    padding: 10px;
    input{
      padding: 10px;
      font-size: 16px;
      border: 2px solid #fff;
      width: 250px;
      color: #0d0f0f;
      font-family: sans-serif;
    }
    button{
      padding: 10px;
      font-size: 16px;
      margin: 10px;
      margin-right: 0px;
      background-color: #1062bb;
      color: #fff;
      border: 1px solid #1916b1;
      &:hover {
        background-color:  #1021bb;
        border: 1px solid #0a1169;
        cursor: pointer;
      }
    }
  }
  ul{
    padding: 10px;

    li{
      position: relative;
      word-wrap: break-word;
      background-color:rgba(#f7f9fb,.5) ;
      color: #333;
      padding: 15px;
      margin-bottom: 15px;
      border-radius: 5px;
      list-style: none;
      min-height: 24px;
      width: 305px;
      display: flex;
      align-items: center;
        p{
          width: 75%;
        }
      button{
        float: right;
        font-size: 12px;
        background-color: #0551fa;
        border-radius: 7px;
        font-weight: bold;
        border: 1px solid #1011c3;
        color: #f4f7f7;
        margin: 5px;
        padding: 7px;
        &:hover{
          cursor: pointer;
        }
      }
    }
  }


}
